body {
  font-family: 'Poppins', sans-serif;  
  background: #2c2c2c;
  color: #fff;
  min-width: 375px;
}

.App {
  font-family: sans-serif;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .hero-color {
    background: transparent !important;
  }
}
.hero-color {
  background: -webkit-gradient(
    linear,
    left top, right top,
    from(transparent),
    color-stop(25%, transparent),
    color-stop(60%, transparent),
    color-stop(60%, #ffb637),
    to(#ffb637)
  );
  background: -webkit-linear-gradient(
    left,
    transparent 0%,
    transparent 25%,
    transparent 60%,
    #ffb637 60%,
    #ffb637 100%
  );
  background: linear-gradient(
    to right,
    transparent 0%,
    transparent 25%,
    transparent 60%,
    #ffb637 60%,
    #ffb637 100%
  );
}

